    width: 125px;
    margin: 0 auto;
    display: block;
}

h1.header-title {
    color: white !important;
    font-size: 66px !important;
    font-weight: 700 !important;
    text-align: center !important;
    text-transform: uppercase;
    margin-top: 2rem;
    text-shadow: 0px 0px 1px #000000;
}

.header-title {
    white-space: nowrap;
}

.header-subtitle {
    visibility: visible;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
}
.header-img h2 {
    color: #4D83D3 !important;
    text-shadow: 0px 0px 3px #ffffff !important;
    font-size: 50px !important;
    white-space: nowrap;
    text-align: center !important;
}

.scroll-downs {
    position: absolute;
    right: 0;
    bottom: 30px;
    left: 0;
    margin: auto;
    width: 25px;
    height: 55px;
    z-index: 1;
}

.mousey {
    width: 1px;
    padding: 10px 10px;
    height: 25px;
    border: 2px solid #fff;
    border-radius: 25px;
    opacity: 0.75;
    box-sizing: content-box;
    box-shadow: 0px 1px 15px 6px rgba(0,0,0,0.75);
}

.scroller {
    width: 3px;
    height: 10px;
    border-radius: 25%;
    background-color: #fff;
    -webkit-animation-name: scroll;
            animation-name: scroll;
    -webkit-animation-duration: 2.2s;
            animation-duration: 2.2s;
    -webkit-animation-timing-function: cubic-bezier(.15,.41,.69,.94);
            animation-timing-function: cubic-bezier(.15,.41,.69,.94);
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes scroll {
    0% { opacity: 0; }
    10% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
    100% { -webkit-transform: translateY(15px); transform: translateY(15px); opacity: 0;}
}

@keyframes scroll {
    0% { opacity: 0; }
    10% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
    100% { -webkit-transform: translateY(15px); transform: translateY(15px); opacity: 0;}
}

@media screen and (max-width: 1200px) {
    h1.header-title {
        -webkit-animation: inherit;
                animation: inherit;
    }
    h2.header-subtitle {
        -webkit-animation: inherit;
